export default {
  data() {
    return {
      ownhost: 'http://192.168.159.129:5000',
      valid: false,
      info: "",
      type: [],
      result: "",
      select_type: [{ state: "文本", abbr: "text" }],
      type_items: [
        { state: "视频", abbr: "video" },
        { state: "文本", abbr: "text" },
        { state: "图片", abbr: "pic" }
      ],
      state: false
    };
  },
  methods: {
    search() {
      this.state = true
      // console.log(this.info)
      axios
        .post("http://192.168.159.129:5000/api", {
          info: this.info,
          type: this.select_type
        })
        .then(res => {
          this.result = res.data;
          this.state = false
        })
        .catch(err => {
          console.log("err:" + err);
          // 异常情况
        });
    }
  }
};
